.about {
  width: 100%;
  height: 100%;
}

.about__frontImg {
  width: 100%;
  height: calc(100vh - 100px);
  min-height: calc(100vh - 100px);
  max-height: calc(100vh - 100px);
  background-image: url("https://images.pexels.com/photos/326278/pexels-photo-326278.jpeg?auto=compress&cs=tinysrgb&w=3000&h=2200&dpr=1");
  background-size: cover;
  background-attachment: fixed;
  background-repeat: no-repeat;
  user-select: none;
  position: relative;
  display: flex;
  align-items: center;
}

.about__frontImg-text {
  position: relative;
  background: transparent;
  max-width: 600px;
}

.about__frontImg-text::before {
  content: "";
  position: absolute;
  background: purple;
  opacity: 0.6;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
}

.about__frontImg-text h1 {
  color: white;
  z-index: 1;
  padding: 7px 20px;
  font-family: Montserrat;
  font-size: 1.9rem;
}
